import { Button, Col, Form, Input, Row } from 'antd';
import moment from 'moment';
import React, { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import ReportDetails from '../components/ReportDetails';

export default function CloseDayPage() {
	const dispatch = useDispatch();
	const history = useHistory();

	const emailRef = useRef(null);
	const passwordRef = useRef(null);
	const [form] = Form.useForm();

	const { loading = false, isGrantedAccess = false } = useSelector((state) => state?.auth);
	const { loading: loadingTrx = false } = useSelector((state) => state?.transaction);
	const { cashier = {}, property = {} } = JSON.parse(localStorage.getItem('app') || '{}');
	const { isEndOfDay = false } = cashier;

	const onFinish = (values) => {
		dispatch({
			type: 'auth/USER_ACCESS',
			payload: {
				email: values?.email,
				password: window.btoa(values?.password),
				funcId: '1038'
			}
		});
	};

	const onFinishFailed = (e) => console.log('Error! ', e);

	const onKeyDown = (e) => {
		if (isGrantedAccess) {
			const isActiveEmail = document.activeElement === (emailRef.current !== null && emailRef.current.input) || false;
			const isActivePassword = document.activeElement === (passwordRef.current !== null && passwordRef.current.input) || false;

			if (e.key === 'Enter' && isActiveEmail) {
				emailRef.current.blur();
				passwordRef.current.focus();
				return;
			}

			if (e.key === 'Enter' && isActivePassword) {
				emailRef.current.blur();
				passwordRef.current.focus();

				onFinish(form.getFieldsValue());

				return;
			}

			if (e.key === 'Enter') {
				form.submit();
			}

			if (e.key === 'Escape') {
				history.push('/pos/options');
				form.resetFields();
			}

			if (e.key === 'ArrowUp') {
				emailRef.current.focus();
			}

			if (e.key === 'ArrowDown') {
				passwordRef.current.focus();

				if (isActivePassword) {
					passwordRef.current.blur();
					emailRef.current.blur();
				}
			}
		}
	};

	const handleCloseDay = () => {
		dispatch({
			type: 'transaction/CLOSING_DAY',
			history,
			property
		});
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDown);
		dispatch({
			type: 'auth/SET_STATE',
			payload: {
				isGrantedAccess: false
			}
		});

		return () => {
			document.removeEventListener('keydown', onKeyDown);
		};
	}, [form]);

	useEffect(() => {
		dispatch({
			type: 'report/SALES_BY_SHIFT',
			payload: {
				refDate: cashier?.shiftDate,
				shiftId: 'all'
			}
		});
	}, [isEndOfDay]);

	return (
		<div className="w-full h-screen overflow-y-scroll">
			<div className="w-full h-full flex flex-col items-center justify-center">
				<div className={`${isEndOfDay ? 'w-2/3 h-full py-10' : 'max-w-xl'}`}>
					<div>
						<h1 className="text-2xl font-medium">Closing Day</h1>
						<p className="text-base leading-snug mt-3">
							Anda sudah membuka kasir pada{' '}
							<span className="font-semibold uppercase text-red-600">
								{moment(cashier?.shiftDate || property?.refDate).format('DD MMMM YYYY')}
							</span>
							. Apakah anda yakin ingin melakukan Closing Day? Pastikan semua data transaksi sudah terupload, dan mohon untuk validasi
							data terlebih dahulu.
						</p>
					</div>

					{!isEndOfDay ? (
						isGrantedAccess ? (
							<div className="w-full flex items-center gap-3 h-auto mt-12">
								<Button className="btn-page" disabled={loadingTrx} loading={loadingTrx} onClick={handleCloseDay}>
									Closing Day
								</Button>
								<Button className="btn-page bg-transparent text-gray-800" onClick={() => history.push('/pos/options')}>
									Batalkan
								</Button>
							</div>
						) : (
							<div className="w-3/4 my-7">
								<Form
									form={form}
									name="login"
									layout="vertical"
									initialValues={{
										remember: true
									}}
									onFinish={onFinish}
									onFinishFailed={onFinishFailed}
									autoComplete="off"
									style={{ maxWidth: 'none', width: '100%' }}>
									<Row wrap gutter={[8, 0]}>
										<Col span={24}>
											<Form.Item
												label="Email"
												name="email"
												rules={[
													{
														required: true,
														message: 'Wajib Diisi!'
													}
												]}>
												<Input
													ref={emailRef}
													type="email"
													autoFocus
													autoComplete="false"
													placeholder="usertest@domain.com"
													className="w-full h-12 rounded-md"
												/>
											</Form.Item>
										</Col>
										<Col span={24}>
											<Form.Item
												label="Password"
												name="password"
												rules={[
													{
														required: true,
														message: 'Wajib Diisi!'
													}
												]}>
												<Input.Password
													ref={passwordRef}
													type="password"
													autoComplete="false"
													placeholder="********"
													className="w-full h-12 rounded-md"
												/>
											</Form.Item>
										</Col>
										<Col span={18}>
											<div className="w-full flex items-center justify-start gap-2 mt-3">
												<Form.Item>
													<Button
														disabled={loading}
														loading={loading}
														type="primary"
														htmlType="submit"
														className="btn-page">
														Validasi Kasir
													</Button>
												</Form.Item>
												<Form.Item>
													<Button
														disabled={loading}
														type="default"
														htmlType="button"
														className="btn-page bg-transparent text-gray-800"
														onClick={() => history.push('/pos/options')}>
														Kembali <kbd className="kbd">esc</kbd>
													</Button>
												</Form.Item>
											</div>
										</Col>
									</Row>
								</Form>
							</div>
						)
					) : (
						<ReportDetails isEndOfDay={true} />
					)}
				</div>
			</div>
		</div>
	);
}
