import { useSelector } from 'react-redux';

export default function useCalculateSales() {
	const { reportSalesShift = {} } = useSelector((state) => state?.report);
	const { balance = [], cashBalance = [], itemCategory = [] } = reportSalesShift;

	const getBalance = (key = '') => {
		if (balance.length !== 0 && key) {
			return balance.reduce((acc, item) => acc + (item[key] || 0), 0);
		}

		return 0;
	};

	const getCashBalance = (key = '') => {
		if (cashBalance.length !== 0 && key) {
			return cashBalance.reduce((acc, item) => acc + (item[key] || 0), 0);
		}

		return 0;
	};

	const getCategoryData = () => {
		const result = itemCategory.reduce((acc, item) => {
			const existing = acc.find((i) => i.cateCode === item.cateCode);

			if (existing) {
				existing.grossSales += item.grossSales;
				existing.salesDiscount += item.salesDiscount;
				existing.nettSales += item.nettSales;
				existing.servValue += item.servValue;
				existing.taxValue += item.taxValue;
			} else {
				acc.push({ ...item });
			}

			return acc;
		}, []);

		return result;
	};

	return { getBalance, getCashBalance, getCategoryData };
}
