import { Col, Row } from 'antd';
import React from 'react';

export default function RowItemPrint({ title = '', value = '', className = '', marginRow = 'my-2' }) {
	return (
		<div className={`w-full ${marginRow}`}>
			<Row>
				<Col span={11} className={`${className !== '' ? className : 'text-base'} capitalize`}>
					{title}
				</Col>
				<Col span={1} className={`${className !== '' ? className : 'text-base'}`}>
					:
				</Col>
				<Col span={12} className={`${className !== '' ? className : 'text-base'} capitalize`}>
					{value}
				</Col>
			</Row>
		</div>
	);
}
