/* eslint-disable array-callback-return */
import { Button } from 'antd';
import moment from 'moment';
import React, { useRef } from 'react';
import { MdOutlineArrowBack } from 'react-icons/md';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { useReactToPrint } from 'react-to-print';
import { currencyFormat } from '../../../utils/helper/utilities';
import useCalculateSales from '../closing-shift/hooks/useCalculateSales';
import RowItemPrint from '../widget/RowItemPrint';
import { ReportPrint } from './ReportPrint';

export default function ReportDetails({ isOpening = false, isClosing = false, isEndOfDay = false, ...props }) {
	const history = useHistory();
	const dispatch = useDispatch();
	let componentPrintRef = useRef();

	const { cashier = {}, user = {} } = JSON.parse(localStorage.getItem('app') || '{}');
	const { reportSalesShift = {}, loadingReportShift = false } = useSelector((state) => state?.report);
	const { itemGroup = [], settlement = [], cashBalance = [] } = reportSalesShift;
	const { getBalance, getCashBalance, getCategoryData } = useCalculateSales();

	const handlePrint = useReactToPrint({
		content: () => componentPrintRef.current,
		onPrintError: (e) => console.log('Error Print!', e),
		removeAfterPrint: true,
		copyStyles: true
	});

	const handleGetReportShift = () => {
		if (isClosing) {
			dispatch({
				type: 'report/SALES_BY_SHIFT',
				payload: {
					refDate: cashier?.shiftDate,
					shiftId: cashier?.shiftId
				}
			});
		} else if (isEndOfDay) {
			dispatch({
				type: 'report/SALES_BY_SHIFT',
				payload: {
					refDate: cashier?.shiftDate,
					shiftId: 'all'
				}
			});
		}
	};

	return (
		<div className="w-full h-auto">
			<div className="w-full mt-7 grid grid-cols-12 gap-5">
				<div className="col-span-8">
					<div className="mb-5">
						<Button className="btn-page w-1/2" loading={loadingReportShift} disabled={loadingReportShift} onClick={handleGetReportShift}>
							Refresh
						</Button>
					</div>
					<div className="w-full">
						<div className="">
							<h1 className="text-lg font-semibold">Report Cashier</h1>
						</div>
						<div className="mt-3">
							<RowItemPrint title="nama kasir" value={user?.username} />
							<RowItemPrint title="opening shift" value={moment(cashier?.shiftDate).format('dddd, DD MMMM YYYY')} />
							{!isEndOfDay && <RowItemPrint title="opening cash" value={`IDR ${currencyFormat(getCashBalance('beginCash'), 0)}`} />}
							<RowItemPrint title="closing shift" value={moment(cashBalance?.refDate).format('dddd, DD MMMM YYYY')} />
							{/* <RowItemPrint title="closing cashier" value={`-`} /> */}
							{!isEndOfDay && (
								<>
									<RowItemPrint title="closing cash" value={`IDR ${currencyFormat(getCashBalance('endCash'), 0)}`} />
									<RowItemPrint title="total cash" value={`IDR ${currencyFormat(getCashBalance('paidValue'), 0)}`} />
									<RowItemPrint title="different" value={`IDR ${currencyFormat(getCashBalance('different'), 0)}`} />
								</>
							)}
						</div>
					</div>
					<div className="w-full bg-gray-400 my-4" style={{ height: '1px' }} />
					<div className="w-full">
						<div className="">
							<h1 className="text-lg font-semibold">Report Sales Balancing</h1>
						</div>
						<div className="mt-3">
							<RowItemPrint title="subtotal" value={'IDR ' + currencyFormat(getBalance('SubTotal'), 0)} />
							<RowItemPrint title="sales discount" value={'IDR ' + currencyFormat(getBalance('DiscValue'), 0)} />
							<RowItemPrint title="discount bill" value={'IDR ' + currencyFormat(getBalance('DiscBill'), 0)} />
							<RowItemPrint title="rounded" value={'IDR ' + currencyFormat(getBalance('Rounded'), 0)} />
							<RowItemPrint title="service tax" value={'IDR ' + currencyFormat(getBalance('ServValue'), 0)} />
							<RowItemPrint title="tax" value={'IDR ' + currencyFormat(getBalance('TaxValue'), 0)} />
							<RowItemPrint title="grand total" value={'IDR ' + currencyFormat(getBalance('GrandTotal'), 0)} />
							<RowItemPrint title="net sales" value={'IDR ' + currencyFormat(getBalance('NetSales'), 0)} />
							<RowItemPrint title="sales collected" value={'IDR ' + currencyFormat(getBalance('SalesCollected'), 0)} />
						</div>
					</div>
					<div className="w-full bg-gray-400 my-4" style={{ height: '1px' }} />
					<div className="w-full">
						<div className="">
							<h1 className="text-lg font-semibold">Report Sales Settlements</h1>
						</div>
						<div className="mt-3">
							{settlement?.map(({ CardName = '', PaidValue = 0 }) => {
								return <RowItemPrint title={CardName.trim()} value={`IDR ${currencyFormat(PaidValue, 0)}`} />;
							})}
						</div>
					</div>
					<div className="w-full bg-gray-400 my-4" style={{ height: '1px' }} />
					<div className="w-full">
						<div className="">
							<h1 className="text-lg font-semibold">Report Sales Detail By Group</h1>
						</div>
						<div className="mt-3">
							{itemGroup?.map(({ groupName = '', nettSales = 0 }) => {
								return <RowItemPrint title={groupName || '-'} value={`IDR ${currencyFormat(nettSales, 0)}`} />;
							})}
						</div>
					</div>
					<div className="w-full">
						<div className="">
							<h1 className="text-lg font-semibold">Report Sales Detail By Category</h1>
						</div>
						<div className="mt-3">
							{getCategoryData()?.map(({ cateName, nettSales }) => {
								return <RowItemPrint title={cateName || '-'} value={`IDR ${currencyFormat(nettSales, 0)}`} />;
							})}
						</div>
					</div>
				</div>
				<div className="col-span-4">
					<div className="w-full mb-3 flex items-center gap-2">
						<Button
							className="flex btn-page px-5 w-max bg-transparent border border-blue-600 text-primary"
							icon={<MdOutlineArrowBack className="text-blue-600" />}
							onClick={() => history.push('/pos/options')}
						/>
						<Button className="btn-page" onClick={(e) => handlePrint(e, () => componentPrintRef)}>
							PRINT {isClosing ? 'CLOSING SHIFT' : isEndOfDay ? 'END OF DAY' : 'REPORT'}
						</Button>
					</div>
					<div className="w-full">
						<ReportPrint
							isOpening={isOpening}
							isClosing={isClosing}
							isEndOfDay={isEndOfDay}
							ref={(el) => (componentPrintRef = el)}
							titlePrint={`REPORT ${isClosing ? 'CLOSING SHIFT' : isEndOfDay ? 'END OF DAY' : 'REPORT'}`}
						/>
					</div>
				</div>
			</div>
		</div>
	);
}
